<template>
  <section class="view-enter-forgot-password__wrapper">
    <div class="container">
      <div class="form-inner">
        <h1 class="page__title">{{ $t("resetPassword") }}</h1>
        <form @submit.prevent="onSubmit()">
          <div class="fieldset">
            <ul class="form-list">
              <li>
                <label for="password" class="required">
                  {{ $t("newPassword") }}
                </label>
                <div class="input-box">
                  <input
                    type="password"
                    :class="[
                      'app-input',
                      requiredPassword && 'app-input--focus',
                    ]"
                    value
                    autocomplete="off"
                    v-model="password"
                    @input="onChangeInput($event, 'password')"
                  />
                </div>
                <span class="error error-danger" v-show="requiredPassword">
                  {{ $t("atLeast6") }}
                </span>
              </li>
              <li>
                <label for="confirmedPassword" class="required">
                  {{ $t("confirmPassword") }}
                </label>
                <div class="input-box">
                  <input
                    type="password"
                    :class="[
                      'app-input',
                      requireConfirmPassword && 'app-input--focus',
                    ]"
                    value
                    autocomplete="off"
                    v-model="confirmedPassword"
                    @input="onChangeInput($event, 'confirmedPassword')"
                  />
                </div>
                <span
                  class="error error-danger"
                  v-show="requireConfirmPassword"
                  >{{ $t("samePasswords") }}</span
                >
              </li>
            </ul>
          </div>
          <div class="view-forgot-password__buttons">
            <button class="btn btn-primary">{{ $t("resetPassword") }}</button>
          </div>
        </form>
      </div>
    </div>
  </section>
</template>

<i18n>
{
  "is": {
    "resetPassword": "Veldu nýtt lykilorð"
  },
  "en": {
    "resetPassword": "Reset a Password"
  }
}
</i18n>

<script>
import { mapActions } from "vuex";
export default {
  name: "ViewEnterForgotPassword",
  data() {
    return {
      password: "",
      confirmedPassword: "",
      requiredPassword: false,
      requireConfirmPassword: false,
      isSubmitted: false,
    };
  },
  components: {},
  methods: {
    ...mapActions(["enterForgotPassword"]),
    async onSubmit() {
      this.isSubmitted = true;
      if (!this.password && !this.confirmedPassword) {
        this.requiredPassword = true;
        this.requireConfirmPassword = true;
        return;
      }
      if (
        this.password.length < 6 &&
        this.confirmedPassword !== this.password
      ) {
        this.requiredPassword = true;
        this.requireConfirmPassword = true;
        return;
      }
      this.requiredPassword = false;
      if (this.confirmedPassword !== this.password) {
        this.requireConfirmPassword = true;
        return;
      }
      this.requireConfirmPassword = false;
      const { id, token } = this.$route.query;
      const inputData = {
        id,
        token,
        password: this.password,
        confirmation: this.confirmedPassword,
        reset: "1",
      };
      const response = await this.enterForgotPassword(inputData);
      if (response && response.status === "success") {
        this.$router.push("/");
      }
    },
    onChangeInput(event, target) {
      if (!this.isSubmitted) return;
      if (target === "password" && this.password.length >= 6) {
        if (this.confirmedPassword.length !== this.password.length) {
          this.requireConfirmPassword = true;
        }
        this.requiredPassword = false;
        return;
      }

      if (this.confirmedPassword.length !== this.password.length) {
        this.requireConfirmPassword = true;
        return;
      }

      if (
        target === "confirmedPassword" &&
        this.confirmedPassword.length === this.password.length
      ) {
        this.requireConfirmPassword = false;
        return;
      }
    },
  },
};
</script>

